import React from 'react'

function Single_project({data,play_video,open_image,set_page_container_z_index}) {
 

  function detect_fun()
  {
    set_page_container_z_index(20);

    if(data.type=="img")
    {
       open_image(data.img_url);
    }else{

      play_video(data.video_id);
    }
  }

  return (
  <>


    <div className={data.type=="img"?"single_project img_light_box":"single_project"} onClick={detect_fun}>
      <span>{
        data.type=="img"?"Click Here":"Scroll Down"
      }</span>

    <div className='project_img_container'>
    <img src={data.img_url} alt={data.heading} />
    </div>
    <h3>{data.heading}</h3>
  </div>
  
  </>
  )
}

export default Single_project